import React, {useState,useEffect} from 'react'
import "./Banner.css"
import axios from "../axios"
import urls from "../Movieurl";


function Banner() {
    const cutText = (string, n) => {
        return string?.length > n ? string.substr(0,n) + "..." : string;

    } 
    const [movie, setMovie]  = useState([]);
    useEffect(()=>{
        async function fetchData(){
            let result = await axios.get(urls.fetchNetflixOriginals);
            
            setMovie(result.data.results[
                Math.floor(Math.random() * 20)
            ]);
        }
        fetchData();
    },[])
    



    // let movies = axios.get('https://api.themoviedb.org/3/movie/550?api_key=15f0ef0787e7dd82c1d0a04a9d5be09d')
    // console.log(movies)

    const bannerStyle = { 
        backgroundSize : "cover",
        backgroundImage : `url('https://image.tmdb.org/t/p/original/${movie.backdrop_path}')`,
        backgroundPosition :"center center"
    }
    return (
        <div className="banner" style={bannerStyle}>
            <div className="banner-contents">
                <h1 className="banner-title">{movie.name}</h1>
                <div className="banner-buttons">
                    <button className="banner-button">Play</button>
                    <button className="banner-button">My LIst</button>
                </div>
                <h1 className="banner-desc">
                    {cutText(movie.overview,120)}
                </h1>
            </div>

            <div className="banner-fadeBottom"/>
        </div>
    )
}

export default Banner
