import React from 'react'
import "./App.css"
import Signup from "./pages/Signup"
import Home from "./pages/Home"

import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";

function App() {

    return (
        <div className="app">
            <Router>
                <Switch>
                    <Route path="/signup">
                        <Signup/>
                    </Route>
                    <Route path="/">
                        <Home/>
                    </Route>
                </Switch>
            </Router>
            

        </div>
    );
}

export default App
// Hook와 같은 사용되는 컴퍼넌트르 부모 컴퍼넌트라고 하고 사용되는 컴퍼넌트는 자식컴퍼넌트라고 한다.
// 1. 부모컴퍼넌트에 스트링으로 값을 정한다 다른 타입일때는 중괄호를 사용 2 자식 컴퍼넌트에서 props로 값을 불러온다.