import React,{useState,useEffect} from 'react'
import "./Nav.css"

function Nav() {
    const [show, setShow] = useState(true);

    const age = 20;
    const type = `${age === 20 && '성인'||'미성년자'}`
    // 조건 && 조건이 참인경우 // 조건이 참이 아닌경우
    // 스크롤이 어느정도 위치까지 가면 show의 값을 true로 덮어 씌우기
    const scrollNavbar = () =>{
      if (window.scrollY > 100) {
          setShow(true);
      } else {
          setShow(false);
      }
    }
    // useEffect는 컴포넌트가 생성, 삭제, 어떤데이터가 수정되는 시점을 캐치해서 그 순간 어떤 소스를 실행
    useEffect(()=>{
        // 컴포넌트가 실행 또는 어떤데이터가 수정되는순간 실행 될 소스
        window.addEventListener("scroll", scrollNavbar)
        //컴포넌트가 삭제되는 순간 실행 될 소스 함수를 return안에
        return( ) => window.removeEventListener("scroll",scrollNavbar);
    }, []) // [변수] 변수값이 수정되는지 감지
    
    return (
        <div className={`nav ${show === true && 'nav-black'}`}>
            <div className="nav-contents">
                <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/2560px-Netflix_2015_logo.svg.png" alt="" className="nav-logo" />
                <img src="https://ih0.redbubble.net/image.618427277.3222/flat,1000x1000,075,f.u2.jpg" alt="" className="nav-avatar" />      
            </div>
        </div>
    )
}

export default Nav
